import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { connect } from "react-redux";
import Time from "react-time";

class BankHeader extends React.Component {
  constructor(props) {
    super(props);
    const currentTime = new Date();
    this.state = {
      timer: 0,
      time: currentTime
    };
  }

  componentDidMount() {
    let interval = setInterval(() => {
      const updatedTime = new Date();
      this.setState({ time: updatedTime });
    }, 1 * 60 * 1000);
    this.setState({ timer: interval });
  }

  componentWillUnmount() {
    clearInterval(this.state.timer);
  }

  render() {
    const { time } = this.state;
    const { user } = this.props;

    return (
      <header className="bankheader">
        <Time
          type="text"
          className="datetimefield"
          value={time}
          format=" DD-MMM-YYYY hh:mm"
        />

        <textarea
          type="text"
          className="bankusername"
          value={`Welcome ${user}`}
          rows="2"
          disabled
        />
      </header>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { firstname, lastname } = state.searchUser;
  return {
    user,
    firstname,
    lastname
  };
}

const connectedBankHeader = connect(mapStateToProps)(BankHeader);
export { connectedBankHeader as BankHeader };



