/**
 * AccountInfo is React container component class renders to accounts page in the Application.
 * The class shows information about the account holder profile
 *
 */
import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { BankHeader } from "./BankHeader";
import { BankHeader2 } from "./BankHeader2";
import BankingSideBar from "./BankingSideBar";
import { TransactionAccount } from "./TransactionAccount";
import { userActions } from "../../actions";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import BankTitle from "./BankTitle";

class AccountInfo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      username: ""
    };
    const { dispatch, user, firstname } = this.props;

    if (!firstname) {
      dispatch(userActions.getAll(user));
    }

    this.handleApprove = this.handleApprove.bind(this);
  }

  handleApprove(e) {
    e.preventDefault();

    const { history } = this.props;
    history.push("/bankapprove");
  }

  render() {
    const { user, address, username } = this.props;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />

          <form className="bankmaindatacontainer">
            <div className="currentposition">You are here: / Profile</div>

            <TransactionAccount />
            <div className="profiledata">
              <div>
                <label className="emaillabel defaultlabelprops">
                  {" "}
                  Email-ID{" "}
                </label>
                <label className="addresslabel defaultlabelprops">
                  {" "}
                  Address{" "}
                </label>
              </div>

              <hr className="accounthr" />

              <div>
                <input
                  type="text"
                  name="bankemail"
                  className="bankemail defaultfieldprops"
                  value={username ? username : user}
                  disabled
                />
                <input
                  type="text"
                  name="address"
                  className="address defaultfieldprops"
                  value={address ? address : " - "}
                  disabled
                />
              </div>
            </div>
            <div align="left" />
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { firstname, username, address } = state.searchUser;
  return {
    user,
    firstname,
    address,
    username
  };
}

const connectedAccountInfoPage = connect(mapStateToProps)(AccountInfo);
export { connectedAccountInfoPage as AccountInfo };



