import React from "react";
import "../styles/style.css";
import "../styles/mobile.css";
import "../styles/tablet.css";
import "../styles/mediumdevice.css";
import Loader from "../components/Loader";
import Header from "../components/header";
import Loginimg from "../resources/Login.png";
import BankLogin from "../resources/BankLogin.jpg";
import DoctorLogin from "../resources/DoctorLogin.jpg";
import PatientLogin from "../resources/PatientLogin.jpg";
import { userActions } from "../actions";
import { connect } from "react-redux";

class OTPLogin extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      otp: "",
      submitted: false
    };
    // The below statusFlag is for breaking PUSH Notification poll
    localStorage.setItem("statusFlag", true);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }


  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }

  getValue() {
    return localStorage.getItem("user");
  }

  getImage() {
    let selection = localStorage.getItem("selected");
    console.log("Selection value - ", selection);
    if (selection === "banking") {
      this.image = BankLogin;
    } else if (selection === "doctor") {
      this.image = DoctorLogin;
    } else {
      this.image = PatientLogin;
    }
    return this.image;
  }

  handleSubmit(e) {
    e.preventDefault();

    var userEmail = document.getElementsByName("username");
    const { defaultValue: email } = userEmail[0];

    this.setState({ submitted: true });
    const { otp } = this.state;
    const { dispatch } = this.props;
    if (email && otp) {
      dispatch(userActions.otpLogin(email, otp));
    }
  }

  render() {
    const { otp, submitted } = this.state;
    return (
      <div>
        <Header />

        <img src={this.getImage()} className="Loginimage" alt="loginimage" />

        <div align="center">
          <form className="loginform" name="form" onSubmit={this.handleSubmit}>
            <div align="center">
              <img src={Loginimg} className="loginicon" alt="Loginlogo" />

              <div className="logintext">
                <span> Login </span>
              </div>

              <div>
                <input
                  type="text"
                  name="username"
                  className="emaildisable"
                  placeholder="Email"
                  value={this.getValue()}
                  disable="true"
                  onChange={this.handleChange}
                />
              </div>
              <div>
                <input
                  type="password"
                  name="otp"
                  className="otp"
                  required="true"
                  placeholder="Enter OTP"
                  value={otp}
                  onChange={this.handleChange}
                />
                {submitted && !otp && (
                  <div className="help-block">OTP is required</div>
                )}
                {!!this.props.failureStatus && (
                  <div className="help-block">Incorrect Username/OTP</div>
                )}
              </div>

              <div className="form-group">
                <button type="submit" className="loginbutton">
                  {submitted && !!this.props.loggingIn ? <Loader /> : "Login"}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { loggingIn, failureStatus } = state.authentication;
  return {
    loggingIn,
    failureStatus
  };
}

const connectedOtpPage = connect(mapStateToProps)(OTPLogin);
export { connectedOtpPage as OTPLogin };



