import { userConstants } from '../constants';

const initialState = {};

export function addData(state = initialState, action) {
  
  switch (action.type) {
    case userConstants.DATA_REQUEST:
      return {
          firstname:action.userinfo.firstname,
          lastname:action.userinfo.lastname,
          email:action.userinfo.email,
          organization:action.userinfo.organization,
          address: action.userinfo.address
    }
    case userConstants.DATA_FAILURE:
      return {};
    default:
      return state
  }
}


