/**
 * BankLanding is React container component class renders to welcome page in the Application.
 * The component renders to main landing page of the bank
 *
 */
import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import Loanicon from "../../resources/LoanIcon.png";
import { BankHeader } from "./BankHeader";
import BankTitle from "./BankTitle";
import { TransactionAccount } from "./TransactionAccount";
import { BankHeader2 } from "./BankHeader2";
import BankingSideBar from "./BankingSideBar";
import { userActions } from "../../actions";
import { connect } from "react-redux";
import { Route, Redirect, Prompt } from "react-router-dom";
import { Link } from "react-router-dom";

class BankLanding extends React.Component {
  constructor(props) {
    super(props);

    const { dispatch } = this.props;

    dispatch({ type: "USERS_POLL_REQUEST" });

    this.state = {
      signout: false,
      isBlocking: false
    };

    this.handleSignOut = this.handleSignOut.bind(this);
    // this.handleClick = this.handleClick.bind(this);
    // this.handleMessage = this.handleMessage.bind(this);
    this.handleWeb = this.handleWeb.bind(this);
    this.handleMobile = this.handleMobile.bind(this);

    localStorage.setItem("statusFlag", true);
  }
  handleWeb(e) {
    e.preventDefault();
    const { history } = this.props;
    history.push("/web");
  }

  handleMobile(e) {
    e.preventDefault();
    const { history } = this.props;
    history.push("/mobile");
  }

  // handleMessage(location) {
  //   location.pathname.startsWith("/app")
  //     ? true
  //     : `Are you sure you want to go to ${location.pathname}?`;
  // }

  // handleClick(e) {
  //   e.preventDefault();
  //   this.setState({ signout: true });
  // }

  handleSignOut(e) {
    e.preventDefault();

    const { dispatch } = this.props;

    this.setState({ signout: true });

    dispatch(userActions.logout());
  }
  componentDidMount() {
    this.setState({ isBlocking: true });
  }

  render() {
    const { user, firstname, lastname } = this.props;
    // if (user === undefined || user === "") {
    //   return (
    //     <div>
    //       <Route>
    //         <Redirect
    //           to={{ pathname: "/", state: { from: this.props.location } }}
    //         />
    //       </Route>
    //     </div>
    //   );
    // }

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />


          <div className="bankmaindatacontainer">
            {/* <div className="currentposition">
              You are here Account Summary
            </div> */}


            <div className="HomePageInformation">

              <div className="homeText">
                <div> This portal is for demonstrating the possible workflows using the below mentioned products.</div>

                <p><b>ActivID AS provides following ActivID applications:</b><br></br>
                  <b>ActivID Authentication Services</b> – consists of the following applications:<br></br>
                  <b>ActivID Authentication Server</b> – The core server that provides the authentication infrastructure to meet cross-channel requirements.
                About the ActivID Authentication Portal – the portal that provides the logon interface for service provider authentication (including the ActivID Management Console and ActivID Self-Service Portal).
                Choose the workflow that you want to explore</p>

                <p><b>HID Approve:</b><br></br>
                The HID® Approve™ is a key component of the ActivID® Authentication Server push-based validation solution whereby logon requests and actions (such as money transfers) can be digitally signed using cryptographic keys stored on a customer’s mobile device
                </p>

                <h4> Please choose your platform from the options below for browsing workflows:</h4>


              </div>
            </div>

            <div className="chooseOption">

              <button className="optionStyle" onClick={this.handleWeb}> Web</button>
              <button className="optionStyle" onClick={this.handleMobile}> Mobile</button>
            </div>

          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { firstname, lastname } = state.searchUser;
  return {
    user,
    firstname,
    lastname
  };
}

const connectedBLandingPage = connect(mapStateToProps)(BankLanding);
export { connectedBLandingPage as BankLanding };



