/**
 * User action creator, Having all the user actions used by redux.
 *
 */
import { userConstants } from "../constants";
import { userService } from "../services";
import { alertActions } from "./";
import { history } from "../helpers";

export const userActions = {
  init,
  login,
  logout,
  register,
  getAll,
  delete: _delete,
  searchUser,
  dataRequest,
  transactDataRequest,
  initiateApprove,
  otpLogin,
  getDevice,
  getApproveStatus,
  getTransactionStatus,
  provisionDevice,
  initiateApproveType,
  updateDetailsRequest,
  updateAPI
};

/**
 * The action function for register user.
 * Function is not used
 * @param {*} user
 */
function dataRequest(user) {
  return dispatch => {
    dispatch(request(user));
    history.push("/register/password");
  };

  function request(user) {
    return {
      type: "DATA_REGISTER_REQUEST",
      userinfo: user
    };
  }
}

/**
 * The action function used in case for transfering data
 * @param {user} user
 */
function transactDataRequest(user) {
  console.log("Called transactDataRequest");
  return dispatch => {
    dispatch(request(user));
  };

  function request(user) {
    return {
      type: "DATA_TRANSFER_REQUEST",
      userinfo: user
    };
  }
}

/**
 * The action function used in case for updating data for registration.
 * The function is not used
 * @param {user} user
 */
function updateDetailsRequest(user) {
  return dispatch => {
    dispatch(request(user));
  };

  function request(user) {
    return {
      type: "DATA_REGISTER_REQUEST",
      userinfo: user
    };
  }
}

/**
 * The function is used during initial selection for the portal.
 * On selection it checks the server for connection
 * The function is used during selection of init. On success it
 * pushes to emailLogin page
 * @param {selection} selection
 */
function init(selection = "") {
  return dispatch => {
    dispatch(
      request({
        selection
      })
    );

    userService.init().then(
      user => {
        dispatch(success(selection));
        // history.push("/emaillogin");
      },
      error => {
        dispatch(failure(error));
        dispatch(alertActions.error(error));
      }
    );
  };

  function request(selection) {
    return {
      type: userConstants.INIT_REQUEST,
      selection
    };
  }

  function success(selection) {
    return {
      type: userConstants.INIT_SUCCESS,
      selection
    };
  }

  function failure(error) {
    return {
      type: userConstants.INIT_FAILURE,
      error
    };
  }
}

function initiateApprove(username, isLogin, message) {
  return dispatch => {
    dispatch(
      request({
        username
      })
    );
    userService.initiateApprove(username, isLogin, message).then(
      user => {
        dispatch(success(user));
        dispatch(this.getApproveStatus(user["auth.req.id"]));
      },
      error => {
        dispatch(failure(error));
        dispatch(alertActions.error(error));
      }
    );
  };

  function request(user) {
    return {
      type: userConstants.APPROVE_REQUEST,
      user
    };
  }

  function success(user) {
    return {
      type: userConstants.APPROVE_SUCCESS,
      user
    };
  }

  function failure(error) {
    return {
      type: userConstants.APPROVE_FAILURE,
      error
    };
  }
}

function initiateApproveType(username, pathLocation, message, amount) {
  console.log("pathlocation - ", pathLocation);
  return dispatch => {
    dispatch(
      request({
        username
      })
    );

    if (amount > 1000) {
      console.log("Amount is > 1000 ", amount);
      userService.getDevice(username).then(
        user => {
          dispatch(successDevice(username));
          dispatch(success(username));
          userService.initiateApprove(username, false, message).then(
            user => {
              console.log("Value in user initiateApproveType ", user);
              dispatch(success(user));
              if (pathLocation) {
                console.log("history push called", pathLocation);
                history.push("/" + pathLocation);
              }
              dispatch(this.getTransactionStatus(user["auth.req.id"]));
            },
            error => {
              dispatch(failure(error));
              dispatch(alertActions.error(error));
            }
          );
        },
        error => {
          dispatch(failureDevice(username));
          let selection = localStorage.getItem("selected");
          if (selection === "banking") {
            history.push("/bankapprove");
          } else {
            history.push("/healthapprove");
          }
        }
      );
    } else {
      console.log("Amount is < 1000 ", amount);

      dispatch(successDevice(username));
      dispatch(success(username));
      const user = userConstants.INITIATE_APPROVE_DATA;
      console.log("Dummy data ", user);
      dispatch(success(userConstants.INITIATE_APPROVE_DATA));
      if (pathLocation) {
        history.push("/" + pathLocation);
      }
      dispatch(successPollResult("APPROVED"));
      localStorage.setItem("transaction", "APPROVED");
    }

    // This is for checking before approval that the user is HID Approve or Not
  };
  function successPollResult(id) {
    console.log("successPollResult ", id);
    return {
      type: userConstants.POLL_SUCCESS,
      id
    };
  }

  function request(user) {
    return {
      type: userConstants.APPROVE_REQUEST,
      user
    };
  }

  function success(user) {
    return {
      type: userConstants.APPROVE_SUCCESS,
      user
    };
  }

  function failure(error) {
    return {
      type: userConstants.APPROVE_FAILURE,
      error
    };
  }

  function successDevice(id) {
    return {
      type: "PUSH_DEVICE_SUCCESS",
      id
    };
  }

  function failureDevice(id, error) {
    return {
      type: "PUSH_DEVICE_FAILURE",
      id,
      error
    };
  }
}
function register(
  username,
  password,
  firstname = "",
  lastname = "",
  organization = ""
) {
  return dispatch => {
    dispatch(
      request({
        username
      })
    );

    userService
      .register(username, password, firstname, lastname, organization)
      .then(
        user => {
          dispatch(success(user));
          history.push("/success");
        },
        error => {
          dispatch(failure(error));
          dispatch(alertActions.error(error));
        }
      );
  };

  function request(user) {
    return {
      type: userConstants.REGISTER_REQUEST,
      user
    };
  }

  function success(user) {
    return {
      type: userConstants.REGISTER_SUCCESS,
      user
    };
  }

  function failure(error) {
    return {
      type: userConstants.REGISTER_FAILURE,
      error
    };
  }
}

/**
 * The function is called in case of login
 * @param {selection} username
 * @param {password} password
 */

function login(username, password) {
  return dispatch => {
    dispatch(
      request({
        username
      })
    );

    userService.login(username, password).then(
      user => {
        dispatch(success(username));
        userService.getDevice(username).then(
          user => {
            history.push("/welcome");
          },
          error => {
            dispatch(userActions.provisionDevice(username));
          }
        );
      },
      error => {
        dispatch(failure(error));
        dispatch(alertActions.error(error));
      }
    );
  };

  function request(user) {
    return {
      type: userConstants.LOGIN_REQUEST,
      user
    };
  }

  function success(user) {
    return {
      type: userConstants.LOGIN_SUCCESS,
      user
    };
  }

  function failure(error) {
    return {
      type: userConstants.LOGIN_FAILURE,
      error
    };
  }
}

/**
 * This function is called in case of OtpLogin
 * @param {selection} username
 * @param {otp} otp
 */

function otpLogin(username, otp) {
  return dispatch => {
    dispatch(
      request({
        username
      })
    );
    //OTP is available for Patient portal only
    userService.otpLogin(username, otp).then(
      user => {
        dispatch(success(username));
        history.push("/bookappointment");
      },
      error => {
        dispatch(failure(error));
        dispatch(alertActions.error(error));
      }
    );
  };

  function request(user) {
    return {
      type: userConstants.LOGIN_REQUEST,
      user
    };
  }

  function success(user) {
    return {
      type: userConstants.LOGIN_SUCCESS,
      user
    };
  }

  function failure(error) {
    return {
      type: userConstants.LOGIN_FAILURE,
      error
    };
  }
}

function logout() {
  userService.logout();
  history.push("/");
  return {
    type: userConstants.LOGOUT
  };
}

function getAll(id) {
  return dispatch => {
    dispatch(request());

    userService.searchUser(id).then(
      user => {
        dispatch(success(id, user));
        localStorage.setItem("FIRSTNAME", user.FIRSTNAME);
        localStorage.setItem("LASTNAME", user.LASTNAME);
      },
      error => dispatch(failure(id, error))
    );
  };

  function request(id) {
    return {
      type: userConstants.SEARCH_REQUEST,
      id
    };
  }

  function success(id, user) {
    return {
      type: userConstants.SEARCH_SUCCESS,
      id,
      user
    };
  }

  function failure(id, error) {
    return {
      type: userConstants.SEARCH_FAILURE,
      id,
      error
    };
  }
}

function _delete(id) {
  return dispatch => {
    dispatch(request(id));

    userService.delete(id).then(
      user => {
        dispatch(success(user));
        history.push("/deletesuccess");
        userService.logout();
        dispatch({
          type: userConstants.LOGOUT
        });
      },
      error => {
        dispatch(failure(id, error));
      }
    );
  };

  function request(id) {
    return {
      type: userConstants.DELETE_REQUEST,
      id
    };
  }

  function success(id) {
    return {
      type: userConstants.DELETE_SUCCESS,
      id
    };
  }

  function failure(id, error) {
    return {
      type: userConstants.DELETE_FAILURE,
      id,
      error
    };
  }
}

function searchUser(id) {
  return dispatch => {
    dispatch(request(id));

    userService.searchUser(id).then(
      user => {
        dispatch(success(id, user));
        localStorage.setItem("user", id);
        localStorage.setItem("FIRSTNAME", user.FIRSTNAME);
        localStorage.setItem("LASTNAME", user.LASTNAME);
        history.push(`/passwordlogin`);
      },
      error => {
        dispatch(failure(id, error));
        if (!error.ok) {
          dispatch(alertActions.error(error));
        }
      }
    );
  };

  function request(id) {
    return {
      type: userConstants.SEARCH_REQUEST,
      id
    };
  }

  function success(id, user) {
    return {
      type: userConstants.SEARCH_SUCCESS,
      id,
      user
    };
  }

  function failure(id, error) {
    return {
      type: userConstants.SEARCH_FAILURE,
      id,
      error
    };
  }
}

function getDevice(username) {
  return dispatch => {
    dispatch(request(username));

    userService.getDevice(username).then(
      user => {
        dispatch(success(username));
        let selection = localStorage.getItem("selected");
        if (selection === "patient") {
          dispatch(setAuthentication(username));
          localStorage.setItem("user", username);
          history.push("/approve");
        } else {
          if (selection === "banking") {
            history.push("/welcome");
          } else if (selection === "doctor") {
            history.push("/issuemedic");
          }
        }
        localStorage.setItem("user", username);
      },
      error => {
        dispatch(failure(username, error));
        const selection = localStorage.getItem("selected");
        if (selection === "banking") {
          history.push("/welcome");
        } else if (selection === "doctor") {
          history.push("/issuemedic");
        } else {
          history.push("/passwordlogin");
        }
        localStorage.setItem("user", username);
      }
    );
  };

  function setAuthentication(user) {
    return {
      type: userConstants.LOGIN_SUCCESS,
      user
    };
  }

  function request(id) {
    return {
      type: "PUSH_DEVICE_REQUEST",
      id
    };
  }

  function success(id) {
    return {
      type: "PUSH_DEVICE_SUCCESS",
      id
    };
  }

  function failure(id, error) {
    return {
      type: "PUSH_DEVICE_FAILURE",
      id,
      error
    };
  }
}

function getApproveStatus(authID) {
  return dispatch => {
    dispatch(request(authID));
    let status = localStorage.getItem("statusFlag");
    if (status === "true") {
      localStorage.setItem("statusFlag", false);
      return;
    }
    userService.getApproveStatus(authID).then(
      user => {
        dispatch(success(user));
        console.log("user getApproveStatus --", user);
        if (
          user.toUpperCase() === "APPROVED" ||
          user.toUpperCase() === "SUCCESS"
        ) {
          console.log("Value in approved getApproveStatus", user);
          history.push("/welcome");
          if (user.toUpperCase() === "APPROVED") {
            localStorage.setItem("approveStatus", "APPROVED");
          }
        } else if (user.toUpperCase() === "UNKNOWN") {
          setTimeout(() => {
            dispatch(this.getApproveStatus(authID));
          }, 3000);

          localStorage.setItem("approveStatus", "UNKNOWN");
        } else {
          history.push("/");
        }
      },
      error => {
        dispatch(failure(authID, error));
        localStorage.setItem("authID", "");
      }
    );
  };

  function request(id) {
    return {
      type: userConstants.POLL_REQUEST,
      id
    };
  }

  function success(id) {
    return {
      type: userConstants.POLL_SUCCESS,
      id
    };
  }

  function failure(id, error) {
    return {
      type: userConstants.POLL_FAILURE,
      id,
      error
    };
  }
}

function getTransactionStatus(authID) {
  return dispatch => {
    dispatch(request(authID));

    userService.getApproveStatus(authID).then(
      user => {
        dispatch(success(user));
        console.log("Status Approve ot UNKNOWN - ", user.toUpperCase());
        if (user.toUpperCase() === "APPROVED") {
          localStorage.setItem("transaction", "APPROVED");
        } else if (user.toUpperCase() === "UNKNOWN") {
          setTimeout(() => {
            dispatch(this.getTransactionStatus(authID));
          }, 3000);
          localStorage.setItem("transaction", "UNKNOWN");
        }
      },
      error => {
        console.log("getTransactionStatus failed");
        dispatch(failure(authID, error));
        localStorage.setItem("authID", "");
      }
    );
  };

  function request(id) {
    return {
      type: userConstants.POLL_REQUEST,
      id
    };
  }

  function success(id) {
    return {
      type: userConstants.POLL_SUCCESS,
      id
    };
  }

  function failure(id, error) {
    return {
      type: userConstants.POLL_FAILURE,
      id,
      error
    };
  }
}

function provisionDevice(username) {
  return dispatch => {
    dispatch(
      request({
        username
      })
    );

    userService.provisionDevice(username).then(
      user => {
        dispatch(success(user));
        history.push("/registerapprove");
      },
      error => {
        dispatch(failure(error));
        dispatch(alertActions.error(error));
      }
    );
  };

  function request(user) {
    return {
      type: userConstants.REGISTER_REQUEST,
      user
    };
  }

  function success(user) {
    return {
      type: userConstants.REGISTER_SUCCESS,
      user
    };
  }

  function failure(error) {
    return {
      type: userConstants.REGISTER_FAILURE,
      error
    };
  }
}

function updateAPI(
  username,
  firstname = "",
  lastname = "",
  organization = "",
  address = ""
) {
  return dispatch => {
    dispatch(
      request({
        username
      })
    );

    userService
      .updateAPI(username, firstname, lastname, organization, address)
      .then(
        user => {
          dispatch(success(user));
        },
        error => {
          dispatch(failure(error));
          dispatch(alertActions.error(error));
        }
      );
  };

  function request(user) {
    return {
      type: userConstants.UPDATE_REQUEST
    };
  }

  function success(user) {
    return {
      type: userConstants.UPDATE_SUCCESS,
      user
    };
  }

  function failure(error) {
    return {
      type: userConstants.UPDATE_FAILURE,
      error
    };
  }
}



