import { userConstants } from "../constants";

const initialState = { pollResult: "UNKNOWN" };

export function startPOLL(state = initialState, action) {
  switch (action.type) {
    case userConstants.POLL_REQUEST:
      return {
        pollResult: "UNKNOWN"
      };
    case userConstants.POLL_SUCCESS:
      return {
        pollResult: action.id
      };
    case userConstants.POLL_FAILURE:
      return {};
    case userConstants.POLL_FORCE:
      return {
        pollResult: "APPROVED"
      };
    default:
      return state;
  }
}



