import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { BankHeader2 } from "./BankHeader2";
import { BankHeader } from "./BankHeader";
import BankingSideBar from "./BankingSideBar";
import { TransactionReceipt } from "../banking/TransactionReceipt";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import Loader from "../../components/Loader";
import BankTitle from "./BankTitle";
import { userConstants } from "../../constants";

class TransactionSuccess extends React.Component {
  constructor(props) {
    super(props);

    if (
      localStorage.getItem("transactionData") === "" ||
      localStorage.getItem("transactionData") === null ||
      localStorage.getItem("transactionData") === undefined
    ) {
      localStorage.setItem("transactionData", "[]");
    }

    // this.props.payeeamount;
    console.log("Call to TransactionSuccess constructor user poll request");
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(e) {
    e.preventDefault();
    const { payeeamount } = this.props;
    const { dispatch, history } = this.props;
    history.push("/transactions");
    console.log("Value in payee amount", payeeamount);
    if (payeeamount <= userConstants.MAX_AMOUNT) {
      console.log("Value in else amount", payeeamount);
      dispatch({ type: "USERS_POLL_FORCE" });
    }
    dispatch({ type: "USERS_POLL_REQUEST" });
  }

  render() {
    const { user, approveStatus } = this.props;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    if ((user !== undefined || user !== "") && !approveStatus) {
      return (
        <div>
          <Route>
            <Redirect
              to={{
                pathname: "/transfer",
                state: { from: this.props.location }
              }}
            />
          </Route>
        </div>
      );
    }

    const { pollResult, payeename, payeeamount, uid } = this.props;
    if (pollResult.toString() !== "UNKNOWN") {
      console.log(
        "call of localStorage TransactionSuccess",
        pollResult,
        " value in localStorage- ",
        localStorage.getItem("transactionData")
      );
      const transactionData = JSON.parse(
        localStorage.getItem("transactionData")
      );
      const dt = new Date();
      const currentDate =
        dt.getDate() + "-" + (dt.getMonth() + 1) + "-" + dt.getFullYear();

      let transact = transactionData.concat({
        name: payeename,
        amount: payeeamount,
        date: currentDate,
        uid: uid,
        pollResult: pollResult
      });

      localStorage.setItem("transactionData", JSON.stringify(transact));
      console.log("Called USERS_POLL_REQUEST", pollResult);
    }

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />

          <div className="bankmaindatacontainer">
            <div className="currentposition">
              You are here: / Payments / Transfer
            </div>

            <div className="labelheading">
              {pollResult === "APPROVED"
                ? "Transaction Successful"
                : pollResult === "DENIED"
                ? "Transaction Rejected "
                : "Approve the Transaction on device"}
            </div>

            <div className="addpayeemessage">
              {pollResult === "APPROVED" ? (
                <TransactionReceipt />
              ) : pollResult === "DENIED" ? (
                "Your transaction cannot be completed"
              ) : (
                "Please check your HID Approve mobile application to approve the new payment"
              )}
            </div>

            <div>
              <button
                type="submit"
                name="continue"
                className="bank-success-continue"
                onClick={this.handleSubmit}
                disabled={
                  pollResult === "APPROVED"
                    ? false
                    : pollResult === "DENIED"
                    ? false
                    : true
                }
              >
                {" "}
                {pollResult === "APPROVED" ? (
                  "Continue"
                ) : pollResult === "DENIED" ? (
                  "Continue"
                ) : (
                  <Loader />
                )}{" "}
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { pollResult } = state.startPOLL;
  const { approveStatus } = state.approveData;
  const { user } = state.authentication;
  const { payeename, payeeamount, uid } = state.transData;
  return {
    pollResult,
    payeename,
    payeeamount,
    user,
    approveStatus,
    uid
  };
}

const connectedTransactionSuccessPage = connect(mapStateToProps)(
  TransactionSuccess
);
export { connectedTransactionSuccessPage as TransactionSuccess };



