import React from "react";
import { Link } from "react-router-dom";
import "../styles/style.css";
import "../styles/mobile.css";
import "../styles/tablet.css";
import "../styles/mediumdevice.css";
import Header from "../components/header";
import Loginimg from "../resources/Login.png";
import BankLogin from "../resources/BankLogin.jpg";
import Alert from "../resources/Alert.png";
import { userActions } from "../actions";
import { connect } from "react-redux";
import DoctorLogin from "../resources/DoctorLogin.jpg";
import PatientLogin from "../resources/PatientLogin.jpg";

class ApproveLogin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      approveSent: false
    };
    this.getFistInCapital = this.getFistInCapital.bind(this);
  }

  componentDidMount() {
    const { dispatch } = this.props;
    this.image = null;
    this.setState({ submitted: true });
    var userEmail = document.getElementsByName("email");
    const { defaultValue: email } = userEmail[0];
    if (email) {
      const firstName = this.getFistInCapital(
        localStorage.getItem("FIRSTNAME")
      );
      const message = `Hello ${firstName} \nPlease approve your login request to the Patient Portal`;
      const isLogin = true;
      dispatch(userActions.initiateApprove(email, isLogin, message));
    }
  }

  getFistInCapital(firstname) {
    return firstname.charAt(0).toUpperCase() + firstname.slice(1);
  }

  componentDidUpdate() {
    const { approveStatus } = this.props;

    if (approveStatus) {
    }
  }

  getValue() {
    return localStorage.getItem("user");
  }

  getImage() {
    let selection = localStorage.getItem("selected");
    console.log("Selection value - ", selection);
    if (selection === "banking") {
      this.image = BankLogin;
    } else if (selection === "doctor") {
      this.image = DoctorLogin;
    } else {
      this.image = PatientLogin;
    }
    return this.image;
  }

  render() {
    return (
      <div>
        <Header />

        <img src={this.getImage()} className="Loginimage" alt="loginimage" />

        <div align="center">
          <form className="loginform" onSubmit={this.handleSubmit}>
            <div align="center">
              <img src={Loginimg} className="loginicon" alt="Loginlogo" />

              <div className="logintext">
                <span> Login </span>
              </div>

              <input
                type="email"
                name="email"
                className="emaildisable"
                value={this.getValue()}
                disabled
              />

              <div>
                <span className="whiteBg2">
                  <div align="left" className="flexrow">
                    <img src={Alert} className="alert" alt="Alert" />

                    <div className="alerttext">Login Approval</div>
                  </div>

                  <p className="alerttext1">
                    Please approve the login on your HID Approve mobile app
                  </p>
                </span>
              </div>
            </div>
            <div align="center">
              <Link to="/otplogin" style={{ textDecoration: "none" }}>
                {" "}
                <span className="logintrouble">
                  {" "}
                  Offline? Login with OTP{" "}
                </span>{" "}
              </Link>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { approveStatus } = state.approveData;
  return {
    approveStatus
  };
}

const connectedApprovePage = connect(mapStateToProps)(ApproveLogin);
export { connectedApprovePage as ApproveLogin };



