/**
 * Statement is React container component class renders to statement page in the Application.
 * It shows the list of transaction account
 */
import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { BankHeader2 } from "./BankHeader2";
import { BankHeader } from "./BankHeader";
import BankingSideBar from "./BankingSideBar";
import { TransactionAccount } from "./TransactionAccount";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import BankTitle from "./BankTitle";

class Statements extends React.Component {
  constructor(props) {
    super(props);

    if (
      localStorage.getItem("transactionData") === "" ||
      localStorage.getItem("transactionData") === null ||
      localStorage.getItem("transactionData") === undefined
    ) {
      localStorage.setItem("transactionData", "[]");
    }

    this.state = {
      username: "",
      searchperiod: " -- Select --",
      submitted: false
    };

    this.handleSearch = this.handleSearch.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    const { name, value } = e.target;
    console.log("the value in ", e.target.value);
    this.setState({ [name]: value });
  }

  /**
   * In Statement component based on the search criteria
   * it redirects to transactions page
   */
  handleSearch(e) {
    e.preventDefault();
    const { history } = this.props;
    const { searchperiod } = this.state;
    if (searchperiod !== undefined && searchperiod !== " -- Select --") {
      history.push("/transactions");
    }
  }

  render() {
    const { user } = this.props;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />

          <div className="bankmaindatacontainer">
            <form>
              <div className="currentposition">
                You are here: / Account History
              </div>

              <TransactionAccount />

              <div className="labelheading view-statements">
                View Accounts History
              </div>

              <div className="addpayeemessage">
                <i>
                  {" "}
                  You can View your Account history for the last 3 months.{" "}
                </i>
              </div>

              <div className="chooseperiod">Search Transactions:</div>

              <div className="flexrow">
                <select
                  className="select"
                  name="searchperiod"
                  onChange={this.handleChange}
                  autoFocus
                >
                  <option> -- Select -- </option>
                  <option> Past 3 weeks </option>
                  <option> Past 6 weeks </option>
                  <option> Past 9 weeks </option>
                  <option> Past 12 weeks </option>
                </select>
                <button
                  type="submit"
                  className="searchhistory"
                  onClick={this.handleSearch}
                >
                  {" "}
                  Search{" "}
                </button>
              </div>

              <div className="note">
                <div className="notetext"> Please Note: </div>

                <div className="notetextpoints">
                  * Transactions of last 3 months will be displayed above by
                  default.
                  <br />* Specified period cannot be greater than 3 months for
                  each enquiry.
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  return {
    user
  };
}

const connectedStatementsPage = connect(mapStateToProps)(Statements);
export { connectedStatementsPage as Statements };



