import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { Link } from "react-router-dom";

class BankingSideBar extends React.Component {
  render() {
    return (
      <div align="left" className="banksidebarcontainer">
        <div id="accountsummary" className="accountsummary">
          <Link
            to="/welcome"
            style={{ textDecoration: "none", color: "white" }}
          >
            Account Summary
          </Link>
        </div>
        <hr className="bankingsidebarhr" />
      </div>
    );
  }
}

export default BankingSideBar;



