import React from "react";
import { Link } from "react-router-dom";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { BankHeader } from "./BankHeader";
import { BankHeader2 } from "./BankHeader2";
import BankingSideBar from "./BankingSideBar";
import { userActions } from "../../actions";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import BankTitle from "./BankTitle";
import { userConstants } from "../../constants/userConstants";

class PayTransfer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      payeename: "",
      payeeamount: "",
      submitted: false,
      signout: false
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleTransaction = this.handleTransaction.bind(this);
  }

  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }

  handleTransaction(e) {
    e.preventDefault();

    const { dispatch, user } = this.props;
    const { payeename, payeeamount } = this.state;

    if (payeename && payeeamount) {
      this.setState({ submitted: true });
      const message = `Authorize transaction of ${payeeamount} for ${payeename} ?`;
      const pathLocation = "transactionsuccess";
      dispatch(
        userActions.transactDataRequest({
          payeename: payeename,
          payeeamount: payeeamount
        })
      );
      dispatch(
        userActions.initiateApproveType(
          user,
          pathLocation,
          message,
          userConstants.GREATER_MAX_AMOUNT
        )
      );
    }
  }

  render() {
    const { user } = this.props;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    let payeeList = JSON.parse(localStorage.getItem("payeeList"));
    let payeeRow = payeeList.map((item, i) => {
      return (
        <tr key={i} align="center">
          <td className="payeetabledata" id="payeetableheading" align="left">
            {" "}
            {item.name}{" "}
          </td>
          <td className="payeetabledata" id="payeetableheading" align="left">
            {/* <Link to="/makepayment" style={{ textDecoration: "none" }}>
              <u> Make Payment </u>
            </Link> */}
            {item.account}/{item.code}
          </td>
        </tr>
      );
    });

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />

          <form className="bankmaindatacontainer">
            <div className="currentposition">You are here: / Payments</div>

            <div className="labelheading">Transfer to the account</div>

            <div className="addpayeemessage">
              If you are transferring funds to a payee for the first time, click
              on 'Add New Payee' to register the payee.
            </div>

            <div>
              <Link to="/addpayee" style={{ textDecoration: "none" }}>
                <button type="submit" className="addpayee">
                  {" "}
                  Add New Payee{" "}
                </button>
              </Link>
              <Link to="/makepayment" style={{ textDecoration: "none" }}>
                <button type="submit" className="addpayee">
                  {" "}
                  Make Payment{" "}
                </button>
              </Link>
            </div>

            <div className="tableoverflow">
              <table
                align="center"
                cellSpacing="0px"
                className="payeetable"
                border="1px"
              >
                <tbody>
                  <tr align="left">
                    <th className="payeetableheading" colSpan="3">
                      Registered Payees
                    </th>
                  </tr>
                  <tr align="center">
                    <th
                      className="payeetableheading"
                      id="payeetableheading"
                      align="left"
                    >
                      {" "}
                      Name{" "}
                    </th>
                    <th
                      colSpan="2"
                      className="payeetableheading"
                      id="payeetableheading"
                      align="left"
                    >
                      {" "}
                      Payee Details{" "}
                    </th>
                  </tr>
                  {payeeRow}
                </tbody>
              </table>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { approveStatus } = state.approveData;
  return {
    user,
    approveStatus
  };
}

const connectedPayTransferPage = connect(mapStateToProps)(PayTransfer);
export { connectedPayTransferPage as PayTransfer };



