/**
 * BankLanding is React container component class renders to welcome page in the Application.
 * The component renders to main landing page of the bank
 *
 */
import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import Loanicon from "../../resources/LoanIcon.png";
import { BankHeader } from "./BankHeader";
import BankTitle from "./BankTitle";
import { TransactionAccount } from "./TransactionAccount";
import { BankHeader2 } from "./BankHeader2";
import BankingSideBar from "./BankingSideBar";
import { userActions } from "../../actions";
import { connect } from "react-redux";
import { Route, Redirect, Prompt } from "react-router-dom";
import { Link } from "react-router-dom";
import DemoLoginPage from "../../resources/DemoLoginPage.png";
import SelfService from "../../resources/selfservice.png";

class WorkflowWeb extends React.Component {
  constructor(props) {
    super(props);

    const { dispatch } = this.props;

    dispatch({ type: "USERS_POLL_REQUEST" });

    this.state = {
      signout: false,
      isBlocking: false
    };

    this.handleSignOut = this.handleSignOut.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleMessage = this.handleMessage.bind(this);
    localStorage.setItem("statusFlag", true);
  }

  handleMessage(location) {
    location.pathname.startsWith("/app")
      ? true
      : `Are you sure you want to go to ${location.pathname}?`;
  }

  handleClick(e) {
    e.preventDefault();
    this.setState({ signout: true });
  }

  handleSignOut(e) {
    e.preventDefault();

    const { dispatch } = this.props;

    this.setState({ signout: true });

    dispatch(userActions.logout());
  }
  componentDidMount() {
    this.setState({ isBlocking: true });
  }

  render() {
    const { user, firstname, lastname } = this.props;
    // if (user === undefined || user === "") {
    //   return (
    //     <div>
    //       <Route>
    //         <Redirect
    //           to={{ pathname: "/", state: { from: this.props.location } }}
    //         />
    //       </Route>
    //     </div>
    //   );
    // }

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />


          <div className="bankmaindatacontainer">
            {/* <div className="currentposition">
              You are here Account Summary
            </div> */}


            <div className="HomePageInformationWeb">

              <div className="homeText">
                ActivID Authentication Service REST API's can be used to integrate Authentication into your web applications to provide security and easy data handling.
                <br></br>ActivID AS acts as an identity provider (IDP) that provides federated, strong, versatile authentication to end users accessing any type of Service Provider (SP) and Single Sign On services.

              <h4>Find more information on ActivID AS from below mentioned documentation:</h4>
                <a href="https://docs.hidglobal.com/activid-as-v8.4/intro/introduction.htm" target="_blank">ActivID Authentication Server Documentation</a>
              </div>
            </div>


            <div className="workflowContainer">
              <div className="containerLeftStyle">
                <div>
                  <br />
                  <div>Demo Application using HID Hardware Token</div>
                  <h4>Products</h4>
                  <p>HID Auth Service</p>
                  <p>HID Approve</p>
                  <p>HID FlexiToken</p>
                </div>
              </div>
              <div className="containerRightStyle">
                <div className="RightElementsAlignment">
                  <img border="0" alt="HID HardwareToken Demo" src={DemoLoginPage} width="300" height="150" />
                  <div><a href="https://hidglobal-dev.konycloud.com/apps/HWTokenDemo/" target="_blank"> HID HardwareToken Demo</a></div>
                </div>
              </div>
            </div>


            <div className="workflowContainer">
              <div className="containerLeftStyle">
                <div>
                  <br />
                  <div>Demo Application using HID Approve Push</div>
                  <h4>Products</h4>
                  <p>HID Auth Service</p>
                  <p>HID Approve</p>
                </div>
              </div>
              <div className="containerRightStyle">
                <div className="RightElementsAlignment">
                  <img border="0" alt="HID Approve Demo" src={DemoLoginPage} width="300" height="150" />
                  <div><a href="https://hidglobal-dev.konycloud.com/apps/HIDApproveDemo/" target="_blank">  HID Approve Demo</a></div>
                </div>
              </div>
            </div>

            <div className="workflowContainer">
              <div className="containerLeftStyle">
                <div>
                  <br />
                  <div>HID Self Service Portal Demo Application</div>
                  <h4>Products</h4>
                  <p>HID Auth Service</p>
                  <p>HID Approve</p>
                </div>
              </div>
              <div className="containerRightStyle">
                <div className="RightElementsAlignment">
                  <img border="0" alt="HID Approve Demo" src={SelfService} width="300" height="150" />
                  <div><a href="https://hidglobal-dev.konycloud.com/apps/HIDUserMgmnt/#_Form1" target="_blank">Self Service Portal</a></div>
                  <div><a href="https://www.youtube.com/watch?v=3uO94pmVuzE" target="_blank">Demonstration Video</a></div>
                </div>
              </div>
            </div>

            <div className="workflowContainer">
              <div className="containerLeftStyle">
              </div>
              <div className="containerRightStyle">

              </div>
            </div>
            {/* <div className="workflowContainer">
              <div className="containerLeftStyle">
                <div>
                  <br />
                  <div>Self Service Portal</div>
                  <h4>Products</h4>
                  <p>HID Auth Service</p>
                  <p>HID Approve</p>
                </div>
              </div>
              <div className="containerRightStyle">
                <img border="0" alt="HID Approve Demo" src={DemoLoginPage} width="300" height="150" />
                <div><a href="https://hidglobal-dev.konycloud.com/apps/HIDApproveDemo/" target="_blank">  HID Approve Demo</a></div>
              </div>
            </div> */}





          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { firstname, lastname } = state.searchUser;
  return {
    user,
    firstname,
    lastname
  };
}

const connectedBLandingPage = connect(mapStateToProps)(WorkflowWeb);
export { connectedBLandingPage as WorkflowWeb };



