import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { Link } from "react-router-dom";
import { userActions } from "../../actions";
import { connect } from "react-redux";
import Loader from "../../components/Loader";
import { BankHeader } from "./BankHeader";
import { BankHeader2 } from "./BankHeader2";
import BankingSideBar from "./BankingSideBar";
import { Route, Redirect } from "react-router-dom";
import BankTitle from "./BankTitle";
import { userConstants } from "../../constants/userConstants";

class AddNewPayee extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      payeename: "",
      payeeaccount: "",
      bankcode: "",
      submitted: false
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleTransaction = this.handleTransaction.bind(this);
  }

  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }

  handleTransaction(e) {
    e.preventDefault();

    const { dispatch, user } = this.props;
    const { payeename, payeeaccount, bankcode } = this.state;

    if (payeename && payeeaccount && bankcode) {
      this.setState({ submitted: true });
      const message = `You are about to add the below beneficiary \n 
    Name: ${payeename} \nAccount Number: ${payeeaccount} \nBank Code: ${bankcode}`;

      const pathLocation = "payeeadded";
      dispatch(
        userActions.transactDataRequest({
          payeename: payeename,
          payeeaccount: payeeaccount,
          bankcode: bankcode
        })
      );
      dispatch(
        userActions.initiateApproveType(
          user,
          pathLocation,
          message,
          userConstants.GREATER_MAX_AMOUNT
        )
      );
    }
  }

  render() {
    const { user } = this.props;
    const { payeename, payeeaccount, bankcode, submitted } = this.state;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />

          <form className="bankmaindatacontainer">
            <div className="currentposition">
              You are here: /{" "}
              <Link
                to="/transfer"
                style={{ color: "white", textDecoration: "none" }}
              >
                {" "}
                Payments{" "}
              </Link>{" "}
              / Add New Payee
            </div>

            <div className="labelheading">Enter New Payee Details</div>

            <div className="addpayeemessage">
              "The new Payee will be added and stored in your Payee List."
            </div>

            <div align="left">
              <div>
                <label className="payeenamelabel">
                  {" "}
                  Name: <span className="red"> * </span>{" "}
                </label>
                <input
                  type="text"
                  className="payeename"
                  name="payeename"
                  value={payeename}
                  onChange={this.handleChange}
                />
              </div>

              <div>
                <label className="payee-account-no-label">
                  {" "}
                  Account Number: <span className="red"> * </span>{" "}
                </label>
                <input
                  type="text"
                  maxLength="14"
                  className="addpayeeaccountnumber"
                  name="payeeaccount"
                  value={payeeaccount}
                  onChange={this.handleChange}
                />
              </div>

              <div>
                <label className="bankcodelabel">
                  {" "}
                  Bank Code: <span className="red"> * </span>{" "}
                </label>
                <input
                  type="text"
                  className="bankcode"
                  name="bankcode"
                  value={bankcode}
                  onChange={this.handleChange}
                />
              </div>

              <div className="flexrow">
                <Link to="/transfer" style={{ textDecoration: "none" }}>
                  <button type="submit" className="cancelnewpayee">
                    {" "}
                    CANCEL{" "}
                  </button>
                </Link>
                <button
                  type="submit"
                  className="submitnewpayee"
                  onClick={this.handleTransaction}
                  disabled={!payeename || !payeeaccount || !bankcode}
                >
                  {" "}
                  {submitted ? <Loader /> : "SUBMIT"}{" "}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { approveStatus } = state.approveData;
  return {
    user,
    approveStatus
  };
}

const connectedAddNewPayeePage = connect(mapStateToProps)(AddNewPayee);
export { connectedAddNewPayeePage as AddNewPayee };



