import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { userActions } from "../../actions";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import IdleTimer from "react-idle-timer";
import { history } from "../../helpers/history";
import { userConstants } from "../../constants/userConstants";

class BankHeader2 extends React.Component {
  constructor(props) {
    super(props);
    this.idleTimer = null;
    this.state = {
      signout: false,
      timeout: userConstants.SESSION_TIMEOUT,
      isIdle: false,
      timer: 0,
      time: 0
    };

    this.handleSignOut = this.handleSignOut.bind(this);
    this.initializeLocalStorage = this.initializeLocalStorage.bind(this);
    this.onActive = this._onActive.bind(this);
    this.onIdle = this._onIdle.bind(this);
    this.getStyle = this.getStyle.bind(this);
    this.initializeLocalStorage();
  }

  componentDidMount() {
    this.setState({
      elapsed: this.idleTimer.getElapsedTime()
    });

    let interval = setInterval(() => {
      this.setState({
        timer: this.idleTimer.getRemainingTime()
      });
    }, 1000);
    this.getStyle();
    this.setState({ time: interval });
  }

  componentWillUnmount() {
    clearInterval(this.state.time);
  }

  initializeLocalStorage() {
    if (
      localStorage.getItem("transactionData") === "" ||
      localStorage.getItem("transactionData") === null ||
      localStorage.getItem("transactionData") === undefined
    ) {
      localStorage.setItem("transactionData", "[]");
    }

    if (
      localStorage.getItem("payeeList") === "" ||
      localStorage.getItem("payeeList") === null ||
      localStorage.getItem("payeeList") === undefined
    ) {
      localStorage.setItem("payeeList", "[]");
    }
  }

  handleSignOut(e) {
    e.preventDefault();

    const { dispatch } = this.props;

    this.setState({ signout: true });

    dispatch(userActions.logout());
  }

  _onActive() {
    // test removal
    // this.setState({ isIdle: false });
    console.log(
      "Value of isIdle in _onActive -- ",
      this.idleTimer.getRemainingTime()
    );
  }

  _onIdle() {
    console.log("Inside isIdle Called");
    const { dispatch } = this.props;
    this.setState({ signout: true });
    alert("User session invalid. Please login again.");
    dispatch(userActions.logout());
  }

  getStyle() {
    let index = 0;
    if (history.location.pathname === "/welcome") {
      index = 0;
    } else if (history.location.pathname === "/web") {
      index = 1;
    } else if (history.location.pathname === "/mobile") {
      index = 2;
    } else if (history.location.pathname === "/accounts") {
      index = 3;
    } else {
      index = 5;
    }
    return index;
  }

  render() {
    return (
      <header className="bankheader2">
        <div className="bankheaderdata2">
          <Link
            to="/"
            style={
              this.getStyle() === 0
                ? { fontWeight: "bold", textDecoration: "none" }
                : { fontWeight: "normal", textDecoration: "none" }
            }
          >
            <div className="myaccount defaultmenuBarData">Home</div>
          </Link>
        </div>
        <div className="bankheaderdata2">
          <Link
            to="/web"
            style={
              this.getStyle() === 1
                ? { fontWeight: "bold", textDecoration: "none" }
                : { fontWeight: "normal", textDecoration: "none" }
            }
          >
            <div className="payments defaultmenuBarData">Web</div>
          </Link>
        </div>
        <div className="bankheaderdata2">
          <Link
            to="/mobile"
            style={
              this.getStyle() === 2
                ? { fontWeight: "bold", textDecoration: "none" }
                : { fontWeight: "normal", textDecoration: "none" }
            }
          >
            <div className="defaultmenuBarData">Mobile</div>
          </Link>
        </div>

        {/* <div className="bankheaderdata2">
          <Link
            to="/banking/contactus"
            style={
              this.getStyle() === 4
                ? { fontWeight: "bold", textDecoration: "none" }
                : { fontWeight: "normal", textDecoration: "none" }
            }
          >
            <div className="contact defaultmenuBarData">Contact Us</div>
          </Link>
        </div> */}
        {/* <div className="bankheaderdata2">
          <div className="banksignout" onClick={this.handleSignOut}>
            Logout
          </div>
        </div> */}
        <IdleTimer
          ref={ref => {
            this.idleTimer = ref;
          }}
          onActive={this.onActive}
          onIdle={this.onIdle}
          timeout={this.state.timeout}
          startOnLoad
        />
      </header>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { firstname, lastname } = state.searchUser;
  return {
    user,
    firstname,
    lastname
  };
}

const connectedBHeader2Page = connect(mapStateToProps)(BankHeader2);
export { connectedBHeader2Page as BankHeader2 };



