/**
 * AppRouter is React component class which is having the routing details.
 * Here it makes a routing decision based on the route path selected or pushed from history API
 */
import React from "react";
import { Router, Route, Switch } from "react-router-dom";
import { history } from "../helpers";
import { connect } from "react-redux";
import Footer from "../components/footer";
import {
  PasswordLogin,
  OTPLogin,
  BankLanding,
  WorkflowWeb,
  WorkflowMobile,
  EmailLogin,
  ApproveLogin,
  PayTransfer,
  DeleteAccount,
  TransactionSuccess,
  TransactionHistory,
  ApproveAfterRegistration,
  BankApprove,
  AddNewPayee,
  MakePayment,
  ContactUs,
  Statements,
  TransactionReceipt,
  AddPayeeSuccess,
  ContactSuccess
} from "../components";
import DeleteSuccess from "../components/DeleteSuccess";
import { AccountInfo } from "../components/banking/AccountInfo";
import { NotFoundPage } from "../components/NotFoundPage";
import "../styles/style.css";
import "../styles/mobile.css";
import "../styles/tablet.css";
import "../styles/mediumdevice.css";
import { userActions } from "../actions";

class AppRouter extends React.Component {
  constructor(props) {
    super(props);

    const { dispatch } = this.props;
    history.listen((location, action) => {
      console.log("The Location in history :", location, "  ", action);
      var actionVar = action.toString();
      console.log("value in actionVar ", actionVar);
      let locationPathname = location.pathname;
      if (
        (locationPathname === "/passwordlogin" ||
          locationPathname === "/otplogin" ||
          locationPathname === "/approve") &&
        actionVar === "POP"
      ) {
        // history.go;
        dispatch(userActions.logout());
      } else {
        console.log("location and action2 is PUSH", location, action);
      }
    });
    console.log("value in public url", `${process.env.PUBLIC_URL}`);
  }

  render() {
    return (
      <div>
        <Router basename={"/banking"} history={history}>
          <Switch>
            <Route exact path="/" component={BankLanding} />
            <Route exact path="/web" component={WorkflowWeb} />
            <Route exact path="/mobile" component={WorkflowMobile} />
            <Route path="/passwordlogin" component={PasswordLogin} />
            <Route path="/otplogin" component={OTPLogin} />
            <Route path="/approve" component={ApproveLogin} />
            <Route path="/welcome" component={BankLanding} />
            <Route path="/transfer" component={PayTransfer} />
            <Route path="/addpayee" component={AddNewPayee} />
            <Route path="/payeeadded" component={AddPayeeSuccess} />
            <Route path="/makepayment" component={MakePayment} />
            <Route path="/contactus" component={ContactUs} />
            <Route path="/contactsuccess" component={ContactSuccess} />
            <Route path="/statements" component={Statements} />
            <Route
              path="/registerapprove"
              component={ApproveAfterRegistration}
            />
            <Route path="/bankapprove" component={BankApprove} />
            <Route path="/transactionsuccess" component={TransactionSuccess} />
            <Route path="/transactionreceipt" component={TransactionReceipt} />
            <Route path="/accounts" component={AccountInfo} />

            <Route path="/transactions" component={TransactionHistory} />
            <Route path="/deleteaccount" component={DeleteAccount} />
            <Route path="/deletesuccess" component={DeleteSuccess} />

            <Route path="/pagenotfound" component={NotFoundPage} />
            <Route component={NotFoundPage} />
          </Switch>
        </Router>

        <Footer />
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { alert } = state;
  return {
    alert
  };
}
const connectedApp = connect(mapStateToProps)(AppRouter);
export { connectedApp as AppRouter };



