/**
 * PasswordLogin is React container component class renders after user is searched successfully
 * Password page is rendered in the Application
 */
import React from "react";
import "../styles/style.css";
import "../styles/mobile.css";
import "../styles/tablet.css";
import "../styles/mediumdevice.css";
import Header from "../components/header";
import Loader from "../components/Loader";
import Loginimg from "../resources/Login.png";
import BankLogin from "../resources/BankLogin.jpg";
import { userActions } from "../actions";
import { userConstants } from "../constants";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import { Link } from "react-router-dom";

class PasswordLogin extends React.Component {
  constructor(props) {
    super(props);
    this.image = null;
    this.state = {
      password: "",
      emptyField: true,
      submitted: false
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.cleanUser = this.cleanUser.bind(this);
  }

  cleanUser() {
    localStorage.removeItem("user");
  }

  /**
   * The function is triggered on change event
   */
  handleChange(e) {
    const { dispatch } = this.props;

    const { name, value } = e.target;
    this.setState({ [name]: value });
    if (name === "password") {
      if (value === "") {
        this.setState({ emptyField: true });
        dispatch({ type: userConstants.LOGIN_RESET });
      } else {
        this.setState({ emptyField: false });
      }
    }
  }

  handleSubmit(e) {
    e.preventDefault();
    const { dispatch, username } = this.props;
    const { password } = this.state;
    this.setState({ submitted: true });

    if (!password) {
      this.setState({ submitted: false });
    }
    if (username && password) {
      dispatch(userActions.login(username, password));
    }
  }

  render() {
    const { username, failureStatus } = this.props;
    const { password, submitted, emptyField } = this.state;

    if (username === undefined || username === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    return (
      <div>
        <Header />

        <img src={BankLogin} className="Loginimage" alt="loginimage" />

        <div align="center">
          <form className="loginform" name="form" onSubmit={this.handleSubmit}>
            <div align="center">
              <img src={Loginimg} className="loginicon" alt="Loginlogo" />

              <div className="logintext">
                <span> Login </span>
              </div>

              <div>
                <input
                  type="text"
                  name="username"
                  className="emaildisable"
                  placeholder="Email"
                  value={username}
                  readOnly
                />

                {submitted && !username && <div>Username is required</div>}
              </div>
              <div>
                <input
                  type="password"
                  name="password"
                  className="password"
                  placeholder="Password"
                  required={true}
                  value={password}
                  onChange={this.handleChange}
                  autoFocus
                  autoComplete="password"
                />
                {submitted && !password}
                {!!failureStatus && !emptyField && (
                  <div className="help-block">Incorrect Username/Password</div>
                )}
              </div>

              <div>
                <button className="loginbutton">
                  {" "}
                  {submitted && !!this.props.loggingIn ? (
                    <Loader />
                  ) : (
                    "Login"
                  )}{" "}
                </button>
                <div align="center">
                  <Link
                    to="/"
                    onClick={this.cleanUser}
                    style={{ textDecoration: "none" }}
                  >
                    {" "}
                    <span className="logintrouble">
                      {" "}
                      Use another account{" "}
                    </span>{" "}
                  </Link>
                </div>
                <div />
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { loggingIn, failureStatus } = state.authentication;
  const { username } = state.searchUser;
  return {
    loggingIn,
    failureStatus,
    username
  };
}

const connectedLoginPage = connect(mapStateToProps)(PasswordLogin);
export { connectedLoginPage as PasswordLogin };



