import React from "react";
import { Link } from "react-router-dom";
import "../styles/style.css";
import "../styles/mobile.css";
import "../styles/tablet.css";
import "../styles/mediumdevice.css";
import Header from "../components/header";

class DeleteSuccess extends React.Component {
  render() {
    return (
      <div>
        <Header />

        <div className="deletesuccessbackground">
          <div align="center" className="deletesuccessmessage">
            Your account has been deleted successfully. Press Continue to go on
            Main Page.
          </div>

          <div align="center">
            <Link to="/" style={{ textDecoration: "none" }}>
              <button type="submit" className="deletesuccess">
                {" "}
                Continue{" "}
              </button>
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default DeleteSuccess;



