/**
 * EmailLogin is React containercomponent class renders to emailpage in the Application
 *
 */
import React from "react";
import "../styles/style.css";
import "../styles/mobile.css";
import "../styles/tablet.css";
import "../styles/mediumdevice.css";
import Loader from "../components/Loader";
import Loginimg from "../resources/Login.png";
import Header from "../components/header";
import BankLogin from "../resources/BankLogin.jpg";
import { userActions } from "../actions";
import { connect } from "react-redux";
import { userConstants } from "../constants";

class EmailLogin extends React.Component {
  constructor(props) {
    super(props);
    this.image = null;
    this.state = {
      username: "",
      submitted: false,
      emptyField: true
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    const { dispatch } = this.props;
    dispatch(userActions.init(""));
  }

  handleSubmit(e) {
    e.preventDefault();
    this.setState({ userstatus: true, submitted: true });
    const { username, submitted } = this.state;
    const { dispatch, searchFailure } = this.props;
    if (username) {
      if (!searchFailure && submitted) {
        this.setState({ submitted: false });
      } else {
        dispatch(userActions.searchUser(username));
      }
    } else {
      this.setState({ userstatus: false, submitted: false });
    }
  }

  /**
   * The function is triggered on change of event
   *
   */
  handleChange(e) {
    const { dispatch } = this.props;
    const { name, value } = e.target;
    this.setState({ [name]: value });
    if (name === "username") {
      if (value === "") {
        this.setState({ emptyField: true });
        dispatch({ type: userConstants.SEARCH_RESET });
      } else {
        this.setState({ emptyField: false });
      }
    }
  }

  render() {
    const { searchFailure } = this.props;
    const { username, submitted, emptyField } = this.state;
    return (
      <div>
        <Header />

        <img src={BankLogin} className="Loginimage" alt="loginimage" />

        <div align="center">
          <form className="loginform" onSubmit={this.handleSubmit}>
            <img src={Loginimg} className="loginicon" alt="Loginlogo" />

            <div className="logintext">
              <span> Login </span>
            </div>
            <div>
              <input
                type="email"
                name="username"
                className="email"
                required={true}
                placeholder="Email address"
                value={username}
                onChange={this.handleChange}
                autoFocus
                autoComplete="email"
              />
              {!!searchFailure && !emptyField && (
                <div className="help-block">User does not exist.</div>
              )}
            </div>
            <div>
              <button type="submit" name="Next" className="nextbutton">
                {" "}
                {submitted && !!this.props.userStatus ? (
                  <Loader />
                ) : (
                  "NEXT"
                )}{" "}
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }
}
function mapStateToProps(state) {
  const { userStatus, searchFailure } = state.searchUser;
  return {
    userStatus,
    searchFailure
  };
}

const connectedEmailPage = connect(mapStateToProps)(EmailLogin);
export { connectedEmailPage as EmailLogin };



