import { userConstants } from '../constants';

const initialState = {};

export function approveData(state = initialState, action) {
  switch (action.type) {
    case userConstants.APPROVE_REQUEST:
      return {
        approveStatus: true,
        authID: action.user
      };
    case userConstants.APPROVE_SUCCESS:
      return {
        approveStatus: true,
        authID: action.user
      };
    case userConstants.APPROVE_FAILURE:
      return {
          approveStatus:false
      };
    case userConstants.APPROVE_RESET:
      return {
          approveStatus:false
      };
    default:
      return state
  }
}


