/**
 * TransactionHistory is React container component class renders to transactions page in the Application.
 * The class shows information about the transactions done based on the search criteria
 *
 */
import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { Link } from "react-router-dom";
import { BankHeader2 } from "./BankHeader2";
import { BankHeader } from "./BankHeader";
import BankingSideBar from "./BankingSideBar";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import BankTitle from "./BankTitle";

class TransactionHistory extends React.Component {
  constructor(props) {
    super(props);

    if (
      localStorage.getItem("transactionData") === "" ||
      localStorage.getItem("transactionData") === null ||
      localStorage.getItem("transactionData") === undefined
    ) {
      localStorage.setItem("transactionData", "[]");
    }

    this.state = {
      username: "",
      signout: false
    };
  }

  render() {
    const { user } = this.props;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    let projectArray = JSON.parse(localStorage.getItem("transactionData"));
    let transactions = projectArray.map((item, i) => {
      return (
        <tr key={i} align="center">
          <td className="payeetabledata"> {item.date} </td>
          <td className="payeetabledata">
            {" "}
            Amount of ${item.amount} has been sent to {item.name}{" "}
          </td>
          <td className="payeetabledata" id="payeetabledata">
            {item.pollResult === "APPROVED" ? "Successful" : "Declined"}
          </td>
        </tr>
      );
    });

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />

          <div className="bankmaindatacontainer">
            <form>
              <div className="currentposition">
                You are here: /{" "}
                <Link
                  to="/statements"
                  style={{ color: "white", textDecoration: "none" }}
                >
                  {" "}
                  Account History{" "}
                </Link>{" "}
                / Transactions
              </div>

              <div className="labelheading">Your Transactions</div>

              <div className="addpayeemessage">
                <i> List of Transactions you have done in last 3 months. </i>
              </div>

              <div>
                <table
                  align="center"
                  cellSpacing="0px"
                  className="payeetable"
                  border="1px"
                >
                  <tbody>
                    <tr align="center">
                      <th className="payeetableheading"> Date </th>
                      <th className="payeetableheading"> Banking Action </th>
                      <th className="payeetableheading"> Result </th>
                    </tr>

                    {transactions}
                  </tbody>
                </table>
              </div>

              <div className="note">
                <div className="notetext"> Please Note: </div>

                <div className="notetextpoints">
                  * Transactions of last 3 months will be displayed above by
                  default.
                  <br />* Specified period cannot be greater than 3 months for
                  each enquiry.
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  return {
    user
  };
}

const connectedTransactionHistoryPage = connect(mapStateToProps)(
  TransactionHistory
);
export { connectedTransactionHistoryPage as TransactionHistory };



