import { combineReducers } from 'redux';

import { authentication } from './authenticationReducer';
import { registration } from './registrationReducer';
import {searchUser} from './searchReducer';
import { alert } from './alertReducer';
import { initializer } from './initReducer';
import { addData} from "./dataReducer";
import {approveData } from "./approveReducer";
import {startPOLL } from './pollReducer';
import {deleteReducer } from './deleteReducer';
import { transData } from './transData';
import { updateDetails } from './updateReducer';


const rootReducer = combineReducers({
  authentication,
  initializer,
  addData,
  searchUser,
  registration,
  transData,
  alert,
  approveData,
  startPOLL,
  deleteReducer,
  updateDetails
});

export default rootReducer;


