import React from "react";
import { Link } from "react-router-dom";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { BankHeader } from "./BankHeader";
import { BankHeader2 } from "./BankHeader2";
import BankingSideBar from "./BankingSideBar";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import BankTitle from "./BankTitle";

class ContactSuccess extends React.Component {
  render() {
    const { user } = this.props;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />

          <form className="bankmaindatacontainer">
            <div className="currentposition">You are here: / Contact Us</div>

            <div className="labelheading">Information Sent Successfully</div>

            <div className="addpayeemessage">
              Click on Continue to go back to your account.
            </div>

            <div>
              <div align="left">
                <Link to="/welcome" style={{ textDecoration: "none" }}>
                  <button type="submit" className="continueupdatesuccess">
                    {" "}
                    Continue{" "}
                  </button>
                </Link>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  return {
    user
  };
}

const connectedContactSuccessPage = connect(mapStateToProps)(ContactSuccess);
export { connectedContactSuccessPage as ContactSuccess };



