import React from "react";
import { Link } from "react-router-dom";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import Loader from "../../components/Loader";
import { BankHeader2 } from "./BankHeader2";
import { BankHeader } from "./BankHeader";
import BankingSideBar from "./BankingSideBar";
import { userActions } from "../../actions";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import BankTitle from "./BankTitle";

class DeleteAccount extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      submitted: false
    };
    this.handleConfirm = this.handleConfirm.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
  }

  handleCancel(e) {
    e.preventDefault();
  }

  handleConfirm(e) {
    e.preventDefault();

    const { dispatch, user } = this.props;

    this.setState({ submitted: true });
    dispatch(userActions.delete(user));
  }

  render() {
    const { user } = this.props;
    const { submitted } = this.state;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />

          <form className="bankmaindatacontainer">
            <div className="currentposition">
              You are here: / My Accounts / Profile / Delete Account
            </div>

            <div className="labelheading">Confirm To Delete Your Account</div>

            <div className="commontext">
              Are you sure you want to delete your account. All your information
              will be deleted permanently.
              <br />
              To delete your account permanently please press confirm.
            </div>

            <div align="center" className="approveButtons flexrow">
              <Link to="/accounts" style={{ textDecoration: "none" }}>
                <button type="submit" name="cancel" className="cancel">
                  {" "}
                  Cancel{" "}
                </button>
              </Link>
              <button
                type="submit"
                name="confirm"
                className="confirm"
                onClick={this.handleConfirm}
              >
                {" "}
                {submitted ? <Loader /> : "Confirm"}{" "}
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { deleting } = state.deleteReducer;
  return {
    user,
    deleting
  };
}

const connectedDeleteAccountPage = connect(mapStateToProps)(DeleteAccount);
export { connectedDeleteAccountPage as DeleteAccount };



