import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { BankHeader } from "./BankHeader";
import { BankHeader2 } from "./BankHeader2";
import BankingSideBar from "./BankingSideBar";
import { connect } from "react-redux";
import Loader from "../../components/Loader";
import { Route, Redirect } from "react-router-dom";
import BankTitle from "./BankTitle";

class AddPayeeSuccess extends React.Component {
  constructor(props) {
    super(props);

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(e) {
    e.preventDefault();
    const { dispatch, history } = this.props;
    history.push("/transfer");
    dispatch({ type: "USERS_POLL_REQUEST" });
  }

  render() {
    const { user } = this.props;
    const { pollResult, payeename, payeeaccount, bankcode } = this.props;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    console.log("AddPayeeSuccess Poll Result ", pollResult);
    if (pollResult !== "undefined" && pollResult === "APPROVED") {
      const payeeData = JSON.parse(localStorage.getItem("payeeList"));
      let payees = payeeData.concat({
        name: payeename,
        account: payeeaccount,
        code: bankcode
      });
      localStorage.setItem("payeeList", JSON.stringify(payees));
    }
    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />

          <form className="bankmaindatacontainer">
            <div className="currentposition">
              You are here: / Payments / Add New Payee
            </div>

            <div className="labelheading">
              {pollResult === "APPROVED"
                ? "Payee Request Successful"
                : pollResult === "DENIED"
                ? "Payee Request Rejected "
                : " Approve Request"}
            </div>

            <div className="addpayeemessage">
              {pollResult === "APPROVED"
                ? "Payee succesfully added to Registered payee list"
                : pollResult === "DENIED"
                ? "Add Payee request is rejected"
                : "Please check your HID Approve mobile application to approve the addition of a new payee"}
            </div>

            <div>
              <div align="left">
                <button
                  type="submit"
                  className="continueupdatesuccess"
                  onClick={this.handleSubmit}
                  disabled={
                    pollResult === "APPROVED"
                      ? false
                      : pollResult === "DENIED"
                      ? false
                      : true
                  }
                >
                  {" "}
                  {pollResult === "APPROVED" ? (
                    "Continue"
                  ) : pollResult === "DENIED" ? (
                    "Continue"
                  ) : (
                    <Loader />
                  )}{" "}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { pollResult } = state.startPOLL;
  const { firstname, lastname, organization } = state.searchUser;
  const { payeename, payeeaccount, bankcode } = state.transData;
  const { approveStatus } = state.approveData;
  return {
    user,
    pollResult,
    firstname,
    lastname,
    organization,
    payeename,
    payeeaccount,
    bankcode,
    approveStatus
  };
}

const connectedAddPayeeSuccessPage = connect(mapStateToProps)(AddPayeeSuccess);
export { connectedAddPayeeSuccessPage as AddPayeeSuccess };



