/**
 * The file is having functions which is interacting with our middleware. Here the API calls are made
 */

import { authHeader } from "../helpers";

export const userService = {
  init,
  login,
  logout,
  register,
  getAll,
  delete: _delete,
  searchUser,
  initiateApprove,
  otpLogin,
  getDevice,
  getApproveStatus,
  provisionDevice,
  updateAPI
};

function init() {
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    }
  };

  return fetch("v1/init", requestOptions).then(response => {
    if (!response.ok) {
      return Promise.reject(response.statusText);
    }
    return response.json();
  });
}

function initiateApprove(username, isLogin, message) {
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify({
      userName: username,
      isLogin: isLogin,
      message: message
    })
  };

  return fetch("v1/user/initapprovetxn", requestOptions).then(response => {
    if (!response.ok) {
      return Promise.reject(response.statusText);
    }
    return response.json();
  });
}

function register(
  username,
  password,
  firstname = "",
  lastname = "",
  organization = ""
) {
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify({
      userName: username,
      password: password,
      fistName: firstname,
      lastName: lastname,
      organization: organization
    })
  };
  return fetch("v1/user", requestOptions).then(response => {
    if (!response.ok) {
      return Promise.reject(response.statusText);
    }
    return response.json();
  });
}

function login(username, password) {
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify({
      userName: username,
      password: password,
      authMode: "PASSWORD",
      otp: ""
    })
  };

  return fetch("v1/user/authn", requestOptions)
    .then(handleResponse)
    .then(json => {
      localStorage.setItem(
        "bearerToken",
        json["activid.enduser.activation.token"]
      );
      localStorage.setItem("user", username);
    });
}

function otpLogin(username, otp) {
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify({
      userName: username,
      password: otp,
      authMode: "OTP",
      otp: ""
    })
  };

  return fetch("v1/user/authn", requestOptions)
    .then(handleResponse)
    .then(json => {
      localStorage.setItem("user", username);
    });
}

function logout() {
  let bearerToken = localStorage.getItem("bearerToken");
  let username = localStorage.getItem("user");
  console.log("username in logout - ", username);
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify({
      userName: username,
      accessToken: bearerToken
    })
  };
  localStorage.clear();
  fetch("v1/user/logout", requestOptions).then(response => {
    console.log("Response - ", response);
    if (!response.ok) {
      console.log("Not successful in logout");
    }
    console.log("Successful in logout");
  });
}

function getAll() {
  const requestOptions = {
    method: "GET",
    headers: authHeader()
  };

  return fetch("/users", requestOptions).then(handleResponse);
}

function _delete(id) {
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    }
  };

  return fetch("v1/user/delete/" + id, requestOptions).then(handleResponse);
}

function searchUser(id) {
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    }
  };

  return fetch("v1/user/search/" + id, requestOptions).then(
    handleSearchResponse
  );
}

function getDevice(username) {
  const requestOptions = {
    method: "GET",
    headers: {
      "Content-Type": "application/json"
    }
  };

  return fetch("v1/user/push/device/" + username, requestOptions).then(
    handleDeviceResponse
  );
}

function getApproveStatus(authID) {
  const requestOptions = {
    method: "GET",
    headers: {
      "Content-Type": "text/plain"
    }
  };

  console.log(
    "https://authserv1.eastus.cloudapp.azure.com/httplistener/cb/status/",
    authID
  );

  return fetch(
    "https://authserv1.eastus.cloudapp.azure.com/httplistener/cb/status/" +
      authID,
    requestOptions
  ).then(handleStatusResponse);
}

function provisionDevice(username) {
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    }
  };
  console.log("provisionDevice ", username);
  return fetch("v1/user/provision/" + username, requestOptions).then(
    handleSearchResponse
  );
}

function updateAPI(username, firstname, lastname, organization, address) {
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify({
      userName: username,
      fistName: firstname,
      lastName: lastname,
      organization: organization,
      fullAddress: address
    })
  };
  return fetch("v1/user/update", requestOptions).then(response => {
    if (!response.ok) {
      return Promise.reject(response.statusText);
    }
    return response.json();
  });
}

function handleResponse(response) {
  if (!response.ok) {
    return Promise.reject(response.statusText);
  }
  return response.json();
}

function handleStatusResponse(response) {
  console.log("handleStatusResponse response 1", response);
  if (!response.ok) {
    console.log("Not Ok");
    return Promise.reject(response.statusText);
  }
  console.log("Ok");
  return Promise.resolve(response.text());
}

function handleSearchResponse(response) {
  if (!response.ok) {
    return Promise.reject(response);
  }
  return response.json();
}

function handleDeviceResponse(response) {
  if (!response.ok) {
    return Promise.reject(response.statusText);
  }
  return response.json();
}



