import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { connect } from "react-redux";
import Transactionaccicon from "../../resources/TransactionAccountsIcon.png";

class TransactionAccount extends React.Component {
  render() {
    const { user, firstname, lastname } = this.props;
    return (
      <div>
        <div className="labelheading">
          <img
            src={Transactionaccicon}
            className="icon"
            alt="Transactionaccicon"
          />
          Transaction Accounts
        </div>

        <div className="transactionaccountinfo-loanaccountinfo">
          <div>
            <label className="accountnamelabel defaultlabelprops">
              {" "}
              Account Name{" "}
            </label>
            <label className="accountnumberlabel defaultlabelprops">
              {" "}
              Account Number{" "}
            </label>
            <label className="totalbalancelabel defaultlabelprops">
              {" "}
              Total Balance{" "}
            </label>
            <label className="availablebalancelabel defaultlabelprops">
              {" "}
              Available Balance{" "}
            </label>
          </div>
          <hr className="accounthr" />
          <div>
            <input
              type="text"
              className="accountname defaultfieldprops"
              name="accountname"
              value={
                firstname !== "" && firstname !== undefined
                  ? firstname + " " + lastname
                  : user
              }
              disabled
            />
            <input
              type="text"
              className="accountnumber defaultfieldprops"
              name="accountnumber"
              value="xxxx-xxxx-7992"
              disabled
            />
            <input
              type="text"
              className="totalbalance-loantype defaultfieldprops"
              name="totalbalance"
              value="$ 6,082.00"
              disabled
            />
            <input
              type="text"
              className="availablebalance-loanamount defaultfieldprops"
              name="availablebalance"
              value="$ 3,246.00"
              disabled
            />
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { firstname, lastname } = state.searchUser;
  return {
    user,
    firstname,
    lastname
  };
}

const connectedTransactionAccount = connect(mapStateToProps)(
  TransactionAccount
);
export { connectedTransactionAccount as TransactionAccount };



