import React from "react";
import { Link } from "react-router-dom";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import logo from "../../resources/Hid.png";

class BankTitle extends React.Component {
  render() {
    return (
      <header className="titlebar flexrow">
        <Link to="/">
          {" "}
          <img
            src={logo}
            align="left"
            className="main-header-logo"
            alt="logo"
          />{" "}
        </Link>

        <div className="titletext"> Product Workflow Portal </div>
      </header>
    );
  }
}

export default BankTitle;



