import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import Transactionsuccessicon from "../../resources/download.png";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";

class TransactionReceipt extends React.Component {
  render() {
    const { user, payeename, payeeamount, payeeaccount } = this.props;

    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    const dt = new Date();
    const currentDate =
      dt.getDate() + "-" + (dt.getMonth() + 1) + "-" + dt.getFullYear();
    return (
      <div className="transactionreceiptcontainer">
        <div className="receiptheading">Payment Successfully Approved</div>

        <div className="flexrow">
          {/* <img
            src={Transactionsuccessicon}
            className="transactionsuccessicon"
            name="transactionsuccess"
            alt="transactionsuccess"
          /> */}

          <table
            align="left"
            cellSpacing="0px"
            cellPadding="2px"
            border="1px"
            className="transactionsuccesstable"
          >
            <tbody>
              <tr align="center">
                <th className="transactionsuccesstabledata">
                  {" "}
                  Transaction Date{" "}
                </th>
                <th className="transactionsuccesstabledata"> Receiver Name </th>
                <th className="transactionsuccesstabledata">
                  {" "}
                  Transaction Account{" "}
                </th>
                <th className="transactionsuccesstabledata">
                  {" "}
                  Transaction Amount{" "}
                </th>
              </tr>
              <tr align="center">
                <td className="transactionsuccessentry"> {currentDate} </td>
                <td className="transactionsuccessentry"> {payeename} </td>
                <td className="transactionsuccessentry"> {payeeaccount} </td>
                <td className="transactionsuccessentry"> {payeeamount} </td>
              </tr>
            </tbody>
          </table>
        </div>

        {/* <div className="approvedtext"> Approved </div> */}
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { payeename, payeeaccount, payeeamount } = state.transData;
  return {
    user,
    payeename,
    payeeaccount,
    payeeamount
  };
}

const connectedTransactionReceiptPage = connect(mapStateToProps)(
  TransactionReceipt
);
export { connectedTransactionReceiptPage as TransactionReceipt };



