import React from "react";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { Link } from "react-router-dom";
import { userActions } from "../../actions";
import { connect } from "react-redux";
import Loader from "../../components/Loader";
import { BankHeader } from "./BankHeader";
import { BankHeader2 } from "./BankHeader2";
import BankingSideBar from "./BankingSideBar";
import { Route, Redirect } from "react-router-dom";
import BankTitle from "./BankTitle";

class MakePayment extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      payeename: "",
      payeeamount: "",
      payeeaccount: "",
      submitted: false
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleTransaction = this.handleTransaction.bind(this);
    this.handleChangeInput = this.handleChangeInput.bind(this);
  }

  handleChangeInput(e, i) {
    e.preventDefault();
    var selectedValue = document.getElementById("payeename").value;
    console.log("Selected value ", selectedValue);
    let payeeList = JSON.parse(localStorage.getItem("payeeList"));
    console.log("Value in payeeList ", payeeList);
    var payeeRow = payeeList.find(item => {
      console.log("item value ", item.name, " selected value ", selectedValue);
      return item.name === selectedValue;
    });
    console.log("Payee Row ", payeeRow);
    if (typeof payeeRow !== "undefined") {
      console.log("Payee account ", payeeRow.account);
      this.setState({
        payeeaccount: payeeRow.account,
        payeename: payeeRow.name
      });
    } else {
      console.log("Payee row empty");
      this.setState({ payeeaccount: "" });
    }
    console.log("State value ", this.state);
  }

  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }

  handleTransaction(e) {
    e.preventDefault();

    const { dispatch, user } = this.props;
    const { payeename, payeeamount, payeeaccount } = this.state;
    console.log("Payeename value ", this.state.payeeamount);
    if (payeeamount) {
      this.setState({ submitted: true });
      const message = `Do you approve the transfer to \n\nName: ${payeename}\nAmount: ${payeeamount}\nAccount Number: ${payeeaccount}`;
      const pathLocation = "transactionsuccess";
      dispatch(
        userActions.transactDataRequest({
          payeename: payeename,
          payeeamount: payeeamount,
          payeeaccount: payeeaccount
        })
      );
      dispatch(
        userActions.initiateApproveType(
          user,
          pathLocation,
          message,
          payeeamount
        )
      );
    } else {
      this.setState({ submitted: false });
    }
  }

  render() {
    const { user } = this.props;
    const { payeename, payeeaccount, payeeamount, submitted } = this.state;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    let payeeList = JSON.parse(localStorage.getItem("payeeList"));
    let list = [{ name: "", account: "" }];
    console.log("Payeelist - ", payeeList.unshift(list));
    let payeeRow = payeeList.map((item, i) => {
      return (
        <option key={i} value={item.name}>
          {item.name}
        </option>
      );
    });

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />
          <form className="bankmaindatacontainer">
            <div className="currentposition">
              You are here: /{" "}
              <Link
                to="/transfer"
                style={{ color: "white", textDecoration: "none" }}
              >
                {" "}
                Payments{" "}
              </Link>{" "}
              / Transfer
            </div>

            <div className="labelheading">Enter Payment Details</div>

            <div className="addpayeemessage">
              <i> (Payment Details) </i>
            </div>

            <div align="left">
              <div>
                <label className="payeenamelabel2">
                  {" "}
                  Name: <span className="red"> * </span>{" "}
                </label>
                {/* <input
                  type="text"
                  className="payeename"
                  name="payeename"
                  value={payeename}
                  onChange={this.handleChange}
                /> */}
                <select
                  className="payeename"
                  name="payeename"
                  id="payeename"
                  onChange={e => this.handleChangeInput(e)}
                >
                  {payeeRow}

                  {/* <option> Savings- xxxx-xxxx-7992 </option> */}
                </select>
              </div>

              <div>
                <label className="payee-to-account">
                  {" "}
                  To Account: <span className="red"> * </span>{" "}
                </label>
                <input
                  type="text"
                  maxLength="14"
                  className="payeeaccountnumber"
                  name="payeeaccount"
                  value={payeeaccount}
                  onChange={this.handleChange}
                />
              </div>

              <div>
                <label className="payee-from-account"> From Account: </label>
                <select className="payeefromaccount" name="payeefromaccount">
                  <option> Savings- xxxx-xxxx-7992 </option>
                </select>
              </div>

              <div>
                <label className="payeeamountlabel">
                  {" "}
                  Amount: <span className="red"> * </span>{" "}
                </label>
                <input
                  type="number"
                  pattern="[0-9]"
                  className="payee-amount"
                  name="payeeamount"
                  value={payeeamount}
                  onChange={this.handleChange}
                />
              </div>

              <div className="flexrow">
                <Link to="/transfer" style={{ textDecoration: "none" }}>
                  <input
                    type="submit"
                    className="cancelnewpayee"
                    value="CANCEL"
                  />
                </Link>
                <button
                  type="submit"
                  className="submitnewpayee"
                  onClick={this.handleTransaction}
                  disabled={!payeeRow || !payeeaccount || !payeeamount}
                >
                  {" "}
                  {submitted ? <Loader /> : "SUBMIT"}{" "}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  const { approveStatus } = state.approveData;
  return {
    user,
    approveStatus
  };
}

const connectedMakePaymentPage = connect(mapStateToProps)(MakePayment);
export { connectedMakePaymentPage as MakePayment };



