
const initialState = {payeename:'',payeeamount:'',medicines:'',bankcode:'', uid:'', quantity:''};

export function transData(state = initialState, action) {
  switch (action.type) {
    case "DATA_TRANSFER_REQUEST":
      return {
          payeename:action.userinfo.payeename,
          payeeamount:action.userinfo.payeeamount,
          payeeaccount:action.userinfo.payeeaccount,
          medicines:action.userinfo.medicines,
          bankcode:action.userinfo.bankcode,
          date:action.userinfo.date,
          quantity:action.userinfo.quantity,
          clinic:action.userinfo.clinic,
          docname:action.userinfo.docname,
          uid:action.userinfo.uid
    }
    case "DATA_TRANSFER_FAILURE":
      return {};
    default:
      return state
  }
}


