import { userConstants } from '../constants';

const initialState = {userStatus:false};

export function searchUser(state = initialState, action) {
  switch (action.type) {
    case userConstants.SEARCH_REQUEST:
      return {
        userStatus: true,
        username: action.id
      };
    case userConstants.SEARCH_SUCCESS:
      return {
        userStatus: true,
        searchFailure: false,
        username: action.id,
        firstname: action.user.FIRSTNAME,
        lastname: action.user.LASTNAME,
        organization: action.user.CMPNY_NAME,
        address: action.user.ADDRESS1
      };
    case userConstants.SEARCH_FAILURE:
      return { userStatus: false, searchFailure: true };
    case userConstants.SEARCH_RESET:
      return { userStatus: false, searchFailure: false };
    case userConstants.LOGOUT:
      return {};
    default:
      return state;
  }
}


