import React from "react";
import { Link } from "react-router-dom";
import "../../styles/style.css";
import "../../styles/mobile.css";
import "../../styles/tablet.css";
import "../../styles/mediumdevice.css";
import { BankHeader2 } from "../banking/BankHeader2";
import { BankHeader } from "../banking/BankHeader";
import BankingSideBar from "../banking/BankingSideBar";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import Loader from "../../components/Loader";
import { userActions } from "../../actions";
import BankTitle from "./BankTitle";

class BankApprove extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      submitted: false
    };

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(e) {
    e.preventDefault();

    this.setState({ submitted: true });
    const { user } = this.props;
    const { dispatch } = this.props;
    if (user !== undefined || user !== "") {
      dispatch(userActions.provisionDevice(user));
    } else {
      this.setState({ submitted: false });
    }
  }

  render() {
    const { user } = this.props;
    const { submitted } = this.state;
    if (user === undefined || user === "") {
      return (
        <div>
          <Route>
            <Redirect
              to={{ pathname: "/", state: { from: this.props.location } }}
            />
          </Route>
        </div>
      );
    }

    return (
      <div>
        <BankTitle />

        <div className="maincontainer">
          <BankHeader2 />

          <BankHeader />

          <BankingSideBar />

          <form className="bankmaindatacontainer">
            <div className="currentposition">
              You are here:/ Enable HID Approve
            </div>

            <div className="labelheading">Confirm To Send Approve Request</div>

            <div className="commontext">
              You have not setup HID Approve for your mobile device. This is
              needed to add new payees.
              <br />
              Please click Continue to setup HID Approve.
            </div>

            <div align="center" className="approveButtons flexrow">
              <Link to="/accounts" style={{ textDecoration: "none" }}>
                <button type="submit" name="cancel" className="cancel">
                  {" "}
                  Cancel{" "}
                </button>
              </Link>

              <button
                type="submit"
                name="confirm"
                className="confirm"
                onClick={this.handleSubmit}
              >
                {" "}
                {submitted ? <Loader /> : "Continue"}{" "}
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.authentication;
  return {
    user
  };
}

const connectedBankApprovePage = connect(mapStateToProps)(BankApprove);
export { connectedBankApprovePage as BankApprove };



